@import "../../App.scss";

.report-section {
  width: 100%;
  padding: 20px 0px;
}

.report-container {
  display: block;
  padding: 20px;
  box-sizing: border-box;
  border: 2px solid black;
  border-radius: 10px;
  background-color: white;
  box-shadow: 5px 5px 0px 0px $color-shadow;
}

.report-title {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 5px;
  text-align: center;
  color: black;
  background-color: white;
  font-size: 20px;
  @include transition-default(all);

  &.minimized {
    position: sticky;
    top: 50px;
    padding: 10px 0 5px 0;
    color: #00000080;
    background-color: #FFFFFFE0;
    font-size: 15px;
    &:hover,
    &:focus {
      color: black;
      background-color: white;
    }
  }
}

.date-range {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  vertical-align: center;
  line-height: 100%;
  gap: 5px;
}

.msgstats-charts {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
}

.chart-container {
  margin: 20px auto;
}

.chart-title {
  font-size: 20px;
  font-weight: bold;
  text-align: center;
  margin-bottom: 20px;
}

.wordfreq-tables-container {
  margin: 20px auto;
  justify-content: center;
}

.wordfreq-tables {
  display: flex;
  flex-direction: row;
  gap: 20px;
  overflow-x: auto;
}

.wordfreq-table {
  margin: 20px auto;

  .wordfreq-table-user {
    font-weight: bold;
  }
}